import React from "react";
import resumeFile from "../documents/resume.pdf";
const parse = require('html-react-parser');

const Resume = ({ classicHeader, darkTheme }) => {
  const educationDetails = [
    {
      yearRange: "2013 - 2016",
      title: "Bachelor Degree",
      place: "Tokyo Institute of Technology",
      desc: "Bachelor Degree in Computer Science",
    },
  ];

  const experienceDetails = [
    {
      yearRange: "2016 - 2017",
      title: "Software Developer",
      place: "Sendyou",
      desc: "Tokyo, Japan<br/> - Joined Sendyou Company as a web developer intern, with a focus on networking and Python, Django web development<br/> - Collaborated closely with the development team, participating in the planning and execution of web development projects. <br/> - Actively engaged in troubleshooting and debugging tasks, working closely with senior developers to identify and resolve issues.<br/> - <b>Technology</b> : Python, PostgreSQL, MongoDB, Flask, Javascript, React, Backend, Software Architecture, Bootstrap, Node.js, MySQL, Data Modeling, Databases, Nginx, Apache, Rest APIs, C++, SQLAlchemy, Full-Stack, ETL, APIs, Test-driven Development",
    },
    {
      yearRange: "2017 - 2018",
      title: "Software Developer",
      place: "Individual",
      desc: "Tokyo, Japan<br/> - Served as a Full Stack Developer at Individual, focusing on node-based frameworks for web development.<br/> - Contributed to the end-to-end development of web applications using frameworks such as Express.js and Node.js, ensuring seamless integration of front-end and back-end components.<br/> - Demonstrated proficiency in database management using frameworks like MySQL, effectively handling data storage, retrieval, and optimization for application requirements.<br/> - Developed an internal system for objective quality measurement of the full-text search system. It allowed product managers to focus on important features with the most benefit for the end-users.<br/> - Actively engaged as a team player, collaborating with cross-functional teams, and actively contributing to team discussions, problem-solving, and delivering high-quality solutions.<br/> - <b>Technologies</b> : Node, Express, Nest.js, React, Typescript, Redux, Tailwind, Bootstrap, SSR, Webpack, MongoDB, PostgreSQL, Firebase, Apache, Nginx, Javascript,CI/CD, Web Security, Pen testing",
    },
    {
      yearRange: "2018 - 2021",
      title: "AI/ML Engineer | Backend Developer",
      place: "Cromai",
      desc: "So Paulo, Brazil<br/> - Designed Georeferenced identification of weed group in sugarcane fields using Deep Learning and OpenCV<br/> - Designed and Implemented computer vision software capable of fast, accurate, and early diagnosis to increase efficiency of producers in  the agriculture industry<br /> - Designed and Implemented image processing and data analysis system with Python, OpenCV, Machine Learning, PostgreSQL<br /> - <b>Technologies</b> : Python, Flask, SQLAlchemy, OpenCV, DeepLearning, MLOps, Architecture, Hardware, Google Vision API, C++, Computer Vision Algorithms, Data Analysis, Pandas, Pytorch, Numpy, Matplotlib, Seaborn,",
    },
    {
      yearRange: "2021 - 2022",
      title: "Sr. Backend Developer | AI Engineer",
      place: "Laskie",
      desc: `USA, Remote<br/> - Designed the matching algorithms combined with people power, aka Sources, to find and vet technical talent.<br/> - Designed and Implemented machine learning search and matching architecture to provide candidates.<br/> - Spearheaded a modernization of the development process by establishing the brand-new roles of DevOps and SRE, which resulted in a faster process of releasing the changes into production.<br/> - <b>Technology</b>: Python, Golang, Data Science, Machine Learning, Elasticsearch, Google Cloud Machine Learning, Data Engineering, MongoDB, Micro Services, Scrum, CI/CD Pipeline, Docker, Docker Compose, Nginx, AI design, PySpark, Web Security, GraphQL, Rest, Pytorch, Flask, Santic Web Server, Pytest, SQL, Linux, Natural Language Toolkit(NLTK), Deep Nural Networks`,
    },
    {
      yearRange: "2023 - 2023",
      title: "AI/NLP Engineer | Backend Developer",
      place: "Makebell",
      desc: `HongKong, Remote <br/> - Designed and implemented a machine learning system that recognizes and extracts meaningful information from accounting documents using deep learning algorithms.<br/> - Designed and implemented ai document review system that detect translation errors of the company fund documents.<br/> - Designed and implemented highly scalable, reliable, and distributed back-end services using Python, FastAPI, Celery, Redis, and MongoDB to process incoming data in real-time.<br/>- Played a key role in developing RESTful APIs using Python frameworks like Flask, ensuring efficient  processing document extracting, pairing, reviewing, evaluate translation quality.<br/>- <b>Techchologies</b> : Python, Data Science, Data Extraction, Machine Learning, Alibaba Machine Learning, Google Cloud Machine Learning, Data Engineering, Architecture, AI Design, Web Security, Nginx, DevOps, Linux, Docker, Jupyter Noteboo, React, Javascript, Document Extracting, Sentence Transformer, Pytorch, Bert Model, RestAPIs, Web Development, Deep Nural Networks, Github CI/CD, SQLAlchemy, Postgres, Numpy, Pandas, AWS Lamda`,
    },
  ];
  // OpenAI, Claude 2, LLama, Langchain, GPT 4, Prompt Engineering, Chatbot Development
  const skills = [
    {
      name: "Python/Django/Flask/FastAPI",
      percent: 95,
    },
    {
      name: "Node/Express/Nest.js/React/Next.js/Typescript/JavaScript",
      percent: 80,
    },
    {
      name: "Golang/Gin/Beego",
      percent: 65,
    },
    {
      name: "Machine Learning/Natural Language Processing",
      percent: 80,
    },
    {
      name: "Data Science/Analysis",
      percent: 90,
    },
    {
      name: "DevOps Engineering/Cloud",
      percent: 95,
    },
    {
      name: "Networking/Cyber Security",
      percent: 80,
    },
    {
      name: "Leadership",
      percent: 90,
    },
  ];

  return (
    <section
      id="resume"
      className={"section " + (darkTheme ? "bg-dark-1" : "")}
    >
      <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
        {/* Heading */}
        <div className="position-relative d-flex text-center mb-5">
          <h2
            className={
              "text-24  text-uppercase fw-600 w-100 mb-0 " +
              (darkTheme ? "text-muted opacity-1" : "text-light opacity-4")
            }
          >
            
          </h2>
          <p
            className={
              "text-9 text-dark fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
              (darkTheme ? "text-white" : "text-dark")
            }
          >
            {" "}
            Resume
            <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
          </p>
        </div>
        {/* Heading end*/}
        <div className="row gx-5">
          {/* My Education */}
          <div className="col-md-12">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Education
            </h2>
            {educationDetails.length > 0 &&
              educationDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                    {value.desc}
                  </p>
                </div>
              ))}
          </div>
          {/* My Experience */}
          <div className="col-md-12">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Experience
            </h2>
            {experienceDetails.length > 0 &&
              experienceDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")} style={{ whiteSpace: 'initial' }}>
                    {parse(value.desc)}
                  </p>
                </div>
              ))}
          </div>
        </div>
        {/* My Skills */}
        <h2
          className={
            "text-6 fw-600 mt-4 mb-4 " + (darkTheme ? "text-white" : "")
          }
        >
          My Skills
        </h2>
        <div className="row gx-5">
          {skills.length > 0 &&
            skills.map((skill, index) => (
              <div key={index} className="col-md-6">
                <p
                  className={
                    " fw-500 text-start mb-2 " +
                    (darkTheme ? "text-light" : "text-dark")
                  }
                >
                  {skill.name}{" "}
                  <span className="float-end">{skill.percent}%</span>
                </p>
                <div
                  className={
                    "progress progress-sm mb-4 " + (darkTheme ? "bg-dark" : "")
                  }
                >
                  <div
                    className="progress-bar"
                    role="progressbar"
                    style={{ width: skill.percent + "%" }}
                    aria-valuenow={skill.percent}
                    aria-valuemin={0}
                    aria-valuemax={100}
                  />
                </div>
              </div>
            ))}
        </div>
        <div className="text-center mt-5">
          <a
            className="btn btn-outline-secondary rounded-pill shadow-none"
            href={resumeFile}
            download
          >
            Download CV
            <span className="ms-1">
              <i className="fas fa-download" />
            </span>
          </a>
        </div>
      </div>
    </section>
  );
};

export default Resume;
